body {
  background-color: white;
}

.center {
  display: grid;
  place-items: center;
  min-height: 100vh;
  margin: 0;
}

.flex {
  display: flex;
  gap: 2rem;
}

html {
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  background-color: #57abf6;
}

.full {
  top: 5%;
  position: absolute;
}

.spot {
  position: absolute;
  background-color: white;
  border-radius: 50%;
  border: black, solid, 5px;
  z-index: 15;
  width: 295px;
  aspect-ratio: 1;
  left: calc(51% - 147.5px);
  top: calc(36% - 147.5px);
}

.shroom_hat {
  background: #1cc648;
  position: relative;
  overflow: hidden;
  border: black solid 5px;
  width: 590px;
  height: 490px;
  border-radius: 50% 50% 40% 40%/65% 65% 35% 40%;
}
.shroom_hat::before {
  content: "";
  position: absolute;
  background-image: radial-gradient(white 0%, white 68.3%, black 68.3%, black 100%);
  border-radius: 50%;
  z-index: 15;
  width: 295px;
  aspect-ratio: 1;
  right: calc(22% - 295px);
  top: calc(62% - 147.5px);
}
.shroom_hat::after {
  content: "";
  position: absolute;
  background-image: radial-gradient(white 0%, white 68.3%, black 68.3%, black 100%);
  border-radius: 50%;
  z-index: 15;
  width: 295px;
  aspect-ratio: 1;
  left: calc(22% - 295px);
  top: calc(62% - 147.5px);
}

.shroom_body {
  background: #f3dd87;
  position: relative;
  border: black solid 5px;
  z-index: 10;
  width: 320px;
  height: 165px;
  left: calc(50% - 160px);
  top: -110px;
  border-radius: 40% 41% 27% 35%/18% 19% 82% 82%;
}
.shroom_body::before {
  content: "";
  position: absolute;
  z-index: 20;
  width: 35px;
  height: 80px;
  background: black;
  left: calc(31% - 17.5px);
  top: 24px;
  border-radius: 43%;
  box-shadow: 130px 0 0 black;
}
.shroom_body::after {
  content: "";
  position: absolute;
  left: calc(34% - 17.5px);
  top: 32px;
  z-index: 20;
  width: 14px;
  height: 24px;
  background-color: #fff;
  box-shadow: 130px 0 0 white;
  border-radius: 43%;
}